import { Component, OnInit, ViewChildren } from '@angular/core';
import { HistoricoService } from '../services/historico.service';
import { faSearch, faSort } from '@fortawesome/free-solid-svg-icons';
import { Historico } from '../models/historico.model';
import { TranslateService } from '@ngx-translate/core';
import { SortEvent, NgbdSortableHeader } from '../directives/sortable.directive';
import { QueryList } from '@angular/core/src/render3';
import { Observable } from 'rxjs';

@Component({
  selector: 'historico-list',
  templateUrl: './historico-list.component.html',
  styleUrls: ['../historico.component.scss']
})
export class HistoricoListComponent implements OnInit {

  faSearch = faSearch;
  
  faSort = faSort;
  grupos$: Observable<any[]>;
  loading$: Observable<boolean>;
  historicos$: Observable<Historico[]>;
  total$: Observable<number>;
  

  static LOCALE_KEY = 'currentLocale';

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  constructor(
            private historicoService: HistoricoService,
            public translate: TranslateService) {
    this.historicos$ = historicoService.items$;
    this.total$ = historicoService.total$;

    translate.setDefaultLang('ptBR');
    const currentLocale = sessionStorage.getItem(HistoricoListComponent.LOCALE_KEY);

    translate.setDefaultLang(currentLocale);

    console.log(currentLocale);
  }
  
  get page() { return this.historicoService.page; }
  get pageSize() { return this.historicoService.pageSize; }
  get searchTerm() { return this.historicoService.searchTerm; }
  set page(page: number) { this.historicoService.page =  page;}
  set pageSize(pageSize: number) { this.historicoService.pageSize =  pageSize; }
  set searchTerm(searchTerm: string) {  this.historicoService.searchTerm = searchTerm; }


  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting items
    this.historicoService.sortColumn = column;
    this.historicoService.sortDirection = direction;
  }

  ngOnInit() {
    this.clear();
  }

  findValues(contrato: string, cpf: string) {
    this.refreshList(contrato, cpf);
  }

  clear(): void {
    this.searchTerm = "";
    this.headers.forEach(header => {
        header.direction = '';
    });
    this.historicoService.clear();
  }
  
  refreshList(contrato: string, cpf: string) {
    console.log(`contrato : ` + contrato + ` cpf: ` + cpf);

    if (contrato === "") {
      contrato = "0";
    }
    if (cpf === "") {
      cpf = "0";
    }
    this.historicoService.load(contrato, cpf);
  }

}
