import { Injectable, PipeTransform } from '@angular/core';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { tap, catchError, finalize, debounceTime, switchMap, delay } from 'rxjs/operators';
import { Historico } from '../models/historico.model';

import { environment } from '../../environments/environment';
import { HandleError, HttpErrorHandler } from './http-error-handler.service';
import { ToastrService } from 'ngx-toastr';
import { State } from '../models/state.model';
import { SortDirection } from '../directives/sortable.directive';
import { SearchResult } from '../models/search-result.model';
import { DecimalPipe } from '@angular/common';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};


function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort(items: Historico[], column: string, direction: string): Historico[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(historico: Historico, grupo: string, text: string, pipe: PipeTransform) {
  const term = text.toLowerCase();
  return historico.codigoGrupo.includes(grupo)
    && (historico.codigoHistorico.toLowerCase().includes(term)
      || historico.dtSolicitacao.toString().includes(term)
      || historico.dtRetorno.toString().includes(term));

}

function validItemB(historico: Historico, codigoGrupo: string, codigoHistorico: string, dtSolicitacao: string, dtRetorno: string) {

  if (dtSolicitacao.length == 9 && dtRetorno.length == 9) {
    if (historico.codigoGrupo.includes(codigoGrupo)) {
      if (historico.codigoHistorico.includes(codigoHistorico)) {
        if (dtSolicitacao.includes(historico.dtSolicitacao.substr(0, 3).concat(historico.dtSolicitacao.substr(4, 6)))) {
          if (dtRetorno.includes(historico.dtRetorno.substr(0, 3).concat(historico.dtRetorno.substr(4, 6)))) {
            return historico
          }
        }
      }
    }
  }
  if (dtSolicitacao.length == 10 && dtRetorno.length == 9) {
    if (historico.codigoGrupo.includes(codigoGrupo)) {
      if (historico.codigoHistorico.includes(codigoHistorico)) {
        if (dtSolicitacao.includes(historico.dtSolicitacao)) {
          if (dtRetorno.includes(historico.dtRetorno.substr(0, 3).concat(historico.dtRetorno.substr(4, 6)))) {
            return historico
          }
        }
      }
    }
  }
  if (dtSolicitacao.length == 9 && dtRetorno.length == 10) {
    if (historico.codigoGrupo.includes(codigoGrupo)) {
      if (historico.codigoHistorico.includes(codigoHistorico)) {
        if (dtSolicitacao.includes(historico.dtSolicitacao.substr(0, 3).concat(historico.dtSolicitacao.substr(4, 6)))) {
          if (dtRetorno.includes(historico.dtRetorno)) {
            return historico
          }
        }
      }
    }
  }
  if (dtSolicitacao.length == 10 && dtRetorno.length == 10) {
    return (historico.codigoGrupo === codigoGrupo
      && historico.codigoHistorico === codigoHistorico
      && dtSolicitacao === historico.dtSolicitacao
      && dtRetorno === historico.dtRetorno)
  }
}
function fullMatch(historico: Historico, codigo: string) {
  return (historico.codigoHistorico === codigo);
}


@Injectable({
  providedIn: 'root'
})
export class HistoricoService {

  //private serviceUrl = 'historico';
  private _serviceName = "Historico";
  private _items$ = new BehaviorSubject<Historico[]>([]);
  private listaItems: Historico[] = [];
  private _loading$ = new BehaviorSubject<boolean>(false);
  private _total$ = new BehaviorSubject<number>(0);
  private _search$ = new Subject<void>();
  private handleError: HandleError;

  private originalCopy: Historico = null;

  private _state: State = {
    page: 1,
    pageSize: 10,
    codigo: '',
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  constructor(private http: HttpClient,
    private httpErrorHandler: HttpErrorHandler,
    private toastr: ToastrService,
    private pipe: DecimalPipe) {
    this.handleError = httpErrorHandler.createHandleError(this._serviceName);

    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._search()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._items$.next(result.items);
      this._total$.next(result.total);
    });
  }

  get items$() { return this._items$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }
  set codigo(codigo: string) { this._set({ codigo }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    //this._search$.next();
  }

  private _search(): Observable<SearchResult<Historico>> {
    const { sortColumn, sortDirection, pageSize, page, searchTerm, codigo } = this._state;

    // 1. sort
    let items = sort(this.listaItems, sortColumn, sortDirection);

    // 2. filter
    items = items.filter(item => matches(item, codigo, searchTerm, this.pipe));
    const total = items.length;

    // 3. paginate
    items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);

    //this.messageService.add("success", this._serviceName + ": Lista de items selecionados");

    return of({ items, total });

  }


  getResourceName(): string {
    return 'historico';
  }

  startNewItem(): Observable<Historico> {
    let selectedItem: Historico = new Historico();
    this.originalCopy = null;
    //this._selectedItem$.next(this.selectedItem);
    return of(selectedItem);
  }

  selectItem(cdHistorico: string): Observable<Historico> {
    if (cdHistorico === null || cdHistorico === undefined) {
      console.log(`${this._serviceName}: Historico selecionado Ã© invÃ¡lido!`);
      let msg = `Bureau escolhido Ã© invÃ¡lido!`;
      this.toastr.success(msg, "CyberMessenger");
      return of(null);
    }
    let selectedItem = this.listaItems.find(item => fullMatch(item, cdHistorico));
    return of(selectedItem);
  }

  /* ValidaÃ§Ãµes */
  validItem(codigoGrupo: string, codigoHistorico: string, dtSolicitacao: string, dtRetorno: string) {
    let items = this.listaItems.filter(item => validItemB(item, codigoGrupo, codigoHistorico, dtSolicitacao, dtRetorno));
    return items.length;
  }

  load(contrato: string, cpf: string): void {
    console.log(this._serviceName + " Lista de histÃ³ricos selecionados");
    this.getHistoricos(contrato, cpf).subscribe(
      data => {
        this.listaItems = data;
        this._items$.next(this.listaItems);
        //this._search$.next();
      }
    );
  }

  getHistoricos(contrato: string, cpf: string): Observable<Historico[]> {
    this._loading$.next(true);
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<Historico[]>(`${environment.serviceUrls.serviceUrl}/listarHistorico/${contrato},${cpf}`)
      .pipe(
        finalize(() => this._loading$.next(false)),
        catchError(this.handleError<Historico[]>('getItems', []))
      );

  }

  clear(){
    this.listaItems = [];
    this._items$.next(this.listaItems);
  }

  rollbackChanges() {
    if (this.originalCopy === null) return;
    let selectedIndex = this.listaItems.findIndex(item => fullMatch(item, this.originalCopy.codigoHistorico));
    this.listaItems[selectedIndex] = this.originalCopy;
    this._items$.next(this.listaItems);
  }

  include(item: Historico, done): void {
    this._loading$.next(true);
    let retVal: Observable<Historico> = this.http.put<Historico>(`${environment.serviceUrls.serviceUrl}`, item, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(_ => console.log(_)),
      catchError(this.handleError<Historico>('Incluir'))
    ).subscribe(data => {
      if (data['entity'] == null) {
        console.log(`${this._serviceName}: Ocorreu um erro durante a inclusÃ£o do Historico.`);
      } else {
        console.log(`${this._serviceName}: Item cÃ³digo=${data['entity']['codigoGrupo']}/${data['entity']['codigoHistorico']} incluido com sucesso!`);
        let msg = `CritÃ©rio ${data['entity']['codigoHistorico']} salvo com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");
        this.listaItems.unshift(data['entity']);
        this._items$.next(this.listaItems);
        done();
      };
    }, error => {
      console.log(`Ocorreu uma falha ao incluir o critÃ©rio ${item['codigoGrupo']}/${item['codigoHistorico']}!: ` + error.error.message);
      this.toastr.error("Ocorreu uma falha ao incluir critÃ©rio!", "CyberMessenger");
    });
  }

  save(items: Historico[], done): void {
    this._loading$.next(true);
    let retVal: Observable<Historico> = this.http.post<Historico>(environment.serviceUrls.serviceUrl, items, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(_ => console.log(_)),
      catchError(this.handleError<Historico>('Salvar'))
    ).subscribe(data => {
      if (data['entity'] == null) {
        console.log(`${this._serviceName}: Ocorreu um erro durante a atualizaÃ§Ã£o do Historico.`);
      } else {
        console.log(`${this._serviceName}: Foram atualizados ${items.length} items com sucesso!`);
        let msg = `Item alterado com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");
        this._items$.next(this.listaItems);
        this._search$.next();
        done();
      };
    }, error => {
      console.log(`Ocorreu uma falha ao salvar critÃ©rios!: ` + error.error.message);
      this.toastr.error("Ocorreu uma falha ao salvar critÃ©rios!", "CyberMessenger");
    });
  }

  remove(rowId: string, done): void {

    this._loading$.next(true);
    let retVal: Observable<Historico> = this.http.delete<Historico>(`${environment.serviceUrls.serviceUrl}/${rowId}`, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(_ => console.log(_)),
      catchError(this.handleError<Historico>('Remover'))
    ).subscribe(data => {
      let itemIndex = this.listaItems.findIndex((item) => item['rowid'] === rowId);
      if (itemIndex >= 0) {
        this.listaItems.splice(itemIndex, 1);
        console.log(`Item [${rowId}] removido com sucesso`);
      }
      let msg = `Item removido com sucesso!`;
      this.toastr.success(msg, "CyberMessenger");
      this._items$.next(this.listaItems);
      done();
    }, error => {
      console.log(`Ocorreu uma falha ao remover critÃ©rio!: ` + error.error.message);
      this.toastr.error("Ocorreu uma falha ao remover critÃ©rio!", "CyberMessenger");
    });
  }
}
