import { Component, OnDestroy, OnInit } from '@angular/core';
import { HistoricoService } from './services/historico.service';
import { Historico } from './models/historico.model';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { release } from '../environments/release';


@Component({
  selector: 'historico',
  templateUrl: './historico.component.html',
  styleUrls: ['./historico.component.scss'],
  providers: [HistoricoService]
})
export class HistoricoComponent implements OnInit, OnDestroy {

  goBackLink: string;
  openType: string = "";
  resourceId: string = "";
  faTimes = faTimes;

  showLoading: boolean = false;
  arqLoading$: Observable<boolean>;

  loadSubs: Subscription;
  release: string = release.version;

  constructor(private route: ActivatedRoute,
    private historicoService: HistoricoService,
    private router: Router
  ) {
    this.arqLoading$ = this.historicoService.loading$;
    this.loadSubs = this.arqLoading$.subscribe((status) => {
      this.showLoading = status;
    });
    this.goBackLink = '/external-' + this.historicoService.getResourceName();
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      console.log(params)
      if (params.hasOwnProperty("openType"))
        this.openType = params.get("openType");
      if (params.hasOwnProperty("code"))
        this.resourceId = params.get("code");
    });
  }

  ngOnDestroy() {
    console.log('Cleaning Historico...');
    if (this.loadSubs != undefined) this.loadSubs.unsubscribe();
  }
}



