import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HistoricoComponent} from './historico.component';
import { HistoricoListComponent } from './list/historico-list.component';
import { HistoricoEditComponent } from './editor/historico-edit.component';
import { ProjectConfigResolver } from './interceptors/project.config.resolver';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: HistoricoComponent,
          children: [
            {path: '', component: HistoricoListComponent},
            {path: ':openType',component: HistoricoEditComponent},
            {path: ':openType/:code',component: HistoricoEditComponent }
          ],resolve: {
            envConfiguration: ProjectConfigResolver
          }}
    ])
  ],
  exports: [RouterModule]
})
export class HistoricoRoutingModule { }