import { DecimalPipe } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbDate, NgbDateParserFormatter, NgbDatepickerI18n, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { faCalendarAlt } from '@fortawesome/free-solid-svg-icons';
import { Subscription } from 'rxjs';
import { I18n, CustomDatepicker } from '../util/CustomDatepicker';
import { BrDateFormatter } from '../util/DateFormatters';

import { Historico } from '../models/historico.model';
import { HistoricoService } from '../services/historico.service';

@Component({
  selector: 'cf-historico-edit',
  templateUrl: './historico-edit.component.html',
  styleUrls: ['../historico.component.scss'],
  providers: [
    I18n, { provide: NgbDatepickerI18n, useClass: CustomDatepicker },
    { provide: NgbDateParserFormatter, useClass: BrDateFormatter }]
})
export class HistoricoEditComponent implements OnInit, OnDestroy {
  subtitle: string;
  resourceId: string;
  openType: string;

  goBackLink: string;
  content: Historico;

  modelDataIni: NgbDateStruct;
  modelDataFim: NgbDateStruct;

  errors: string;

  static LOCALE_KEY = 'currentLocale';

  contentForm = new FormGroup({
    "codigoGrupo": new FormControl('', [
			Validators.required,
      Validators.maxLength(1)
    ]),
    "codigoHistorico": new FormControl('', [
      Validators.required,
      Validators.maxLength(4)
    ]),
    "descHistorico": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "dtSolicitacao": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "dtRetorno": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "rowId": new FormControl('')
  });

  faCalendarAlt = faCalendarAlt;
  historicoSubs: Subscription;

  constructor(private route: ActivatedRoute,
    private router: Router,
    private historicoService: HistoricoService,
    private toastr: ToastrService,
    public translate: TranslateService) {
    translate.setDefaultLang('ptBR');
    const currentLocale = sessionStorage.getItem(HistoricoEditComponent.LOCALE_KEY);
    translate.setDefaultLang(currentLocale);

    this.goBackLink = '/external-' + this.historicoService.getResourceName();
  }

  ngOnInit() {
    this.resourceId = this.route.snapshot.paramMap.get('code');
    this.openType = this.route.snapshot.paramMap.get('openType');
    this.subtitle = 'HISTORICO.ITEMSUBTITLE.' + this.openType.toUpperCase();

    if (this.resourceId === null || this.resourceId == undefined)
      this.historicoSubs = this.historicoService.startNewItem()
        .subscribe(item => {
          this.content = item;
          //this.contentForm.patchValue(this.content);
        });
    else
      this.historicoSubs = this.historicoService.selectItem(this.resourceId)
        .subscribe(item => {
          this.content = (item === null) ? this.content : item;
        });

    let arrIni = this.content.dtSolicitacao.split("/");
    this.modelDataIni = NgbDate.from({ year: Number(arrIni[2]), month: Number(arrIni[1]), day: Number(arrIni[0]) });
    let arrFim = this.content.dtRetorno.split("/");
    this.modelDataFim = NgbDate.from({ year: Number(arrFim[2]), month: Number(arrFim[1]), day: Number(arrFim[0]) });
    //console.log(this.content.tipoComissao);
    this.contentForm.patchValue(this.content);
    this.errors = '';

    //this.originalCopy =JSON.parse(JSON.stringify(this.content));
    this.errors = '';
  }

  ngOnDestroy() {
    console.log('Cleaning Criteriosrem...');
    if (this.historicoSubs != undefined) {
      this.historicoSubs.unsubscribe();
      this.historicoSubs.remove(this.historicoSubs);
    }
  }

  onDataSolicChange(selected: NgbDate){
    this.content.dtSolicitacao = `${selected.day}/${selected.month}/${selected.year}`;
  }

  onDataRetornoChange(selected: NgbDate){
      this.content.dtRetorno = `${selected.day}/${selected.month}/${selected.year}`;
  }

  save() {
    switch (this.openType) {
      case 'remove':
        this.historicoService.remove(this.content.codigoHistorico, () => this.exit());
        break;
      case 'change':
        console.log(this.historicoService.validItem(this.content.codigoGrupo,this.content.codigoHistorico,this.content.dtSolicitacao,this.content.dtRetorno));
        if(this.content.dtRetorno > this.content.dtSolicitacao){
          let msg =  `Data de SolicitaÃ§Ã£o nÃ£o pode ser maior que a data de retorno`; 
          this.toastr.error(msg, "Erro:");
        }
       if(this.historicoService.validItem(this.content.codigoGrupo,this.content.codigoHistorico,this.content.dtSolicitacao,this.content.dtRetorno) === 1 ){
         this.historicoService.save([this.content], () => this.exit());
        }
        else{
          let msg =  `Faixa sobreposta em um periodo vigente`; 
          this.toastr.error(msg, "Erro:");
        }
        break;
        
      case 'create':
       if(this.content.dtRetorno > this.content.dtSolicitacao){
          let msg =  `Data de SolicitaÃ§Ã£o nÃ£o pode ser maior que a data de retorno`; 
          this.toastr.error(msg, "Erro:");
       }
       if(this.historicoService.validItem(this.content.codigoGrupo,this.content.codigoHistorico,this.content.dtSolicitacao,this.content.dtRetorno) === 0){
          this.historicoService.include(this.content, () => this.exit());
        }
        else{
          let msg =  `Faixa sobreposta em um periodo vigente`; 
          this.toastr.error(msg, "Erro:");
        }
        break;
    
      default:
        break;
    }
  }

  cancel(){
    this.historicoService.rollbackChanges();
    this.exit();
  }

  exit(): void {
    this.router.navigate([this.goBackLink]);
  }

}
